<template>
  <main>
    <section class="main-left">
      <!-- 轮播 -->
      <el-carousel class="_roundImg" :interval="5000" :autoplay="true" @change="swipeChange($event)" arrow="always" :height="carouselHei + 'px'">
        <el-carousel-item v-for="item in bgImg" :key="item.url" :name="item.url">
          <img class="carousel-img" :src="item.src" :alt="item.url" @click="toBlogDetail(item.url+'')">
          <h3 class="carousel-title text-center" @click="toBlogDetail(item.url+'')">
            {{$store.state.initialBlogData[$store.state.initialBlogData.length - item.url].title}}
          </h3>
        </el-carousel-item>
      </el-carousel>

      <!-- 最新内容 -->
      <p class="latest-text">
        Latest 10
      </p>
      <cl-blog v-for="item in recentTen" :title="item.title" :url="item.url" :key="item.url" :time="item.time" :tags="item.tags" :brief="item.brief"></cl-blog>
    </section>
    <!-- 个人信息 -->
    <section class="main-right">
      <cl-info></cl-info>
    </section>
    <div style="clear:both"></div>
    <!-- 提示弹窗 start -->
    <el-dialog title="温馨提示" :visible.sync="dialogVisible" width="30%">
      <p>新版博客已迁移至
        <a href="https://wwww.scssl.cn" target="_blank">https://www.scssl.cn</a>。
      </p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">留下看看</el-button>
        <el-button type="primary" @click="dialogVisible = false"><a style="color:#fff" href="https://www.scssl.cn" target="_blank">立即前往</a></el-button>
      </span>
    </el-dialog>
    <!-- 提示弹窗 end -->
  </main>
</template>
<script>
// import img1 from '../../assets/img/1.png';// 不好
import img2Default from '../../assets/img/2_default.png';
import img3Default from '../../assets/img/3_default.png';
// import img4Default from '../../assets/img/4_default.png'
import img5Default from '../../assets/img/5_default.png';
import img6Default from '../../assets/img/6_default.png';
import img2 from '../../assets/img/2.png';
import img3 from '../../assets/img/3.png';
// import img4 from '../../assets/img/4.png'
import img5 from '../../assets/img/5.png';
import img6 from '../../assets/img/6.png';
// import img7 from '../../assets/img/7.png'
// import img8 from '../../assets/img/8.png'
const imgArr = [img2, img3, img6, img5];
export default {
  name: 'home',
  data () {
    const that = this;
    return {
      dialogVisible: true,
      carouselHei: 380,
      bgImg: [
        { url: '117', src: img2Default },
        { url: '82', src: img3Default },
        { url: '48', src: img6Default },
        { url: '33', src: img5Default }
      ],
      recentTen: that.getRecentBlog()
    };
  },
  methods: {
    // 轮播图切换懒加载图片
    swipeChange (curIndex) {
      this.bgImg[curIndex].src = imgArr[curIndex];
    },
    // 推荐最新 10篇
    getRecentBlog () {
      const that = this;
      let _recent = [];
      that.$store.state.initialBlogData.forEach((e, i) => {
        if (i <= 10) _recent.push(e);
        else return false;
      });
      return _recent;
    },
    // 跳转至详情页
    toDetails (type, path) {
      if (type === 0) this.$router.push('/blog/blog_' + path);
      else if (type === 1) this.$router.push('/demo/demo_' + path);
    },
    // 转到博客详情
    toBlogDetail (url) {
      this.$router.push('/blog/blog_' + url);
    },
    // 根据屏幕大小 设置轮播图高度
    setCarouselHei () {
      const round = document.getElementsByClassName('_roundImg')[0];
      const minHei = 130;
      const maxHei = 300;
      let calcHei = '';
      if (round) calcHei = (parseInt(window.getComputedStyle(round).width) / 2.5) >> 0;
      else return false;
      this.carouselHei = calcHei < minHei ? minHei : (calcHei > maxHei ? maxHei : calcHei);
    }
  },
  mounted () {
    if (location.hash === '#/home') document.title = '个人博客 by 龙';
    this.setCarouselHei();
    window.onresize = () => this.setCarouselHei();
  }
};
</script>



